:root {
  --black-color: #000;
  --blue-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header__wrap {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.header__logo {
  display: block;
}

.nav__links {
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  list-style: none;
}

li:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: var(--black-color);
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover, .nav__links li:first-child .nav__link {
  color: var(--blue-color);
}

